.center-lecheniya {
  @include sections("linear", "base");
  position: relative;
  overflow: hidden;

  &::before {
    content: "";
    position: absolute;
    inset: 0;
    z-index: 0;
    background-image: url(/assets/img/intro/circles.svg);
    background-position: center 600px;
    background-size: 113%;
    background-repeat: no-repeat;
  }

  &__body {
    display: flex;
    flex-direction: column;
    align-items: center;
    color: $color-white;
    position: relative;
    z-index: 2;
  }
  &__title {
    max-width: 1050px;
    line-height: 1.1;
    text-align: center;
  }
  &__caption {
    max-width: 815px;
    text-align: center;
    margin-block-end: 88px;

    @media (width<=650px) {
      margin-block-end: 24px;
    }
  }
  &__tabs {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(3, 26.85%);
    gap: 73px;
    align-items: start;
    justify-content: center;
    margin-block-end: 112px;

    @media (width<=1390px) {
      grid-template-columns: 80%;
      gap: 10px;
    }

    @media (width<=800px) {
      grid-template-columns: 1fr;
      margin-block-end: 24px;
    }
  }
  &__tab {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    padding: 10px;
    padding-inline-start: 30px;
    border-radius: 8px;
    background-color: $color-white;
    color: $accent-color;
    font-weight: 600;
    font-size: clamp(1rem, 0.5833rem + 1.3333vw, 1.5rem);

    &:nth-child(2) {
      margin-block-start: 32px;

      @media (width<=1390px) {
        margin: 0;
      }
    }

    &::after {
      content: "";
      display: block;
      background-color: $color-white;
      position: absolute;
      width: 26px;
      aspect-ratio: 1/1;
      background-image: url(/assets/img/icons/ellipse.svg);
      background-repeat: no-repeat;
      background-position: center center;
      background-size: 8px;
      border-radius: 50%;
      font-size: 30px;
      left: calc(50% - 13px);
      bottom: -42px;

      @media (width<=1390px) {
        content: none;
      }
    }

    &__icon {
      width: 60px;
      aspect-ratio: 1/1;
      padding: 20px 18px;
      background-color: $accent-color;
      border-radius: 6px;
      display: flex;
      align-items: center;
      justify-content: center;
    }

    @media (width<=800px) {
      padding: 6px;

      &__icon {
        width: 48px;
        padding: 16px 14px;
      }
    }
  }

  &__gallery {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 20px;

    &__wrapper {
      justify-content: center;

      @media (width<=800px) {
        width: 100%;
      }
    }
    &__item {
      width: clamp(18.75rem, 13.2813rem + 17.5vw, 25.3125rem);
      aspect-ratio: 1/1;
      overflow: hidden;
      border-radius: 25px;

      picture img {
        object-fit: cover;
        object-position: center center;
      }

      &_more {
        background: url(/assets/img/intro/circles-mini.png), $accent-color;
        background-position: center center;
        background-size: 170%;
        background-repeat: no-repeat;
        box-shadow: 1px 1px 7px 1px rgb(255, 255, 255, 0);
        transition: box-shadow 300ms ease-out, filter 300ms;
        color: $color-white;

        a {
          display: flex;
          height: 100%;
          justify-content: center;
          align-items: center;
          width: 100%;

          p {
            max-width: 135px;
            text-align: center;
            font-weight: 600;
            font-size: 24px;
          }
        }

        &:hover {
          box-shadow: 1px 1px 7px 1px $accent-color;
          filter: contrast(1.1);
        }

        @media (width<=800px) {
          display: none;
        }
      }
    }

    @media (width<=1800px) {
      display: grid;
      grid-template-columns: 1fr 1fr;
    }

    @media (width<=800px) {
      gap: 0;
      align-items: initial;
      justify-content: initial;
    }
  }

  .btn_consult {
    margin-block-start: 24px;
    .point-mark {
      width: 48px;
    }

    @media (width>800px) {
      display: none;
    }
  }
}
